$wColor: #ffffff;
$color: #eaeaea;
$bgColor: #f8f8f8;
$gText: #7e7e82;
$dText: #1b1c21;

@font-face {
  font-family: 'Comic';
  src: url('../fonts/comic.ttf') format('truetype');
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: 'Regular';
  src: url(../fonts/TTLakes-Regular.woff) format('woff');
  font-weight: normal;
}
@font-face {
  font-family: 'Medium';
  src: url(../fonts/TTLakes-Medium.woff) format('woff');
  font-weight: 400;
}
@font-face {
  font-family: 'TTLakes-Bold';
  src: url(../fonts/TTLakes-Bold.woff) format('woff');
  font-weight: 700;
}
